import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { map, startWith } from 'rxjs/operators';

export interface User {
  cnic: string;
}

@Component({
  selector: 'app-search-client',
  templateUrl: './search-client.component.html',
  styleUrls: ['./search-client.component.scss']
})
export class SearchClientComponent implements OnInit {

  everyData: boolean = false;
  myControl = new FormControl();

  options: User[] = [
    { cnic: '37493-1243423-3' },
    { cnic: '37133-1245793-3' },
    { cnic: '37433-5441223-3' }
  ];

  filteredOptions!: Observable<User[]>;

  constructor() { }

  ngOnInit(): void {
    this.filteredOptions = this.myControl.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.cnic),
        map(cnic => cnic ? this._filter(cnic) : this.options.slice())
      );
  }

  displayFn(user: User): string {
    return user && user.cnic ? user.cnic : '';
  }
  private _filter(cnic: string): User[] {
    const filterValue = cnic;
    return this.options.filter(option => option.cnic.indexOf(filterValue) === 0);
  }

  onSelectionCNIC(e: any) {
    this.everyData = true;
    console.log(e)
  }

}
