import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { Component, NgZone, OnInit, ViewChild } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl
} from '@angular/forms';
import { CustomValidators } from 'ngx-custom-validators';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { take } from 'rxjs/operators';

export interface MaritalStatus {
  name: string;
}

@Component({
  selector: 'app-client-registration',
  templateUrl: './client-registration.component.html',
  styleUrls: ['./client-registration.component.scss']
})
export class ClientRegistrationComponent implements OnInit {

  options1: MaritalStatus[] = [{ name: 'Single' }, { name: 'Divorced' }, { name: 'Widow' }];
  filteredOptions: Observable<MaritalStatus[]> = Object.create(null);

  @ViewChild('autosize', { static: true })
  autosize: CdkTextareaAutosize = Object.create(null);


  public form: FormGroup = Object.create(null);

  constructor(private fb: FormBuilder, private ngZone: NgZone) { }
  ngOnInit() {
    this.form = this.fb.group({
      firstName: [
        null,
        Validators.compose([
          Validators.required,
          Validators.minLength(5),
          Validators.maxLength(15)
        ])
      ],
      middleName: [
        null,
        Validators.compose([
          Validators.required,
          Validators.minLength(5),
          Validators.maxLength(15)
        ])
      ],
      lasttName: [
        null,
        Validators.compose([
          Validators.required,
          Validators.minLength(5),
          Validators.maxLength(15)
        ])
      ],
      nickName: [
        null,
        Validators.compose([
          Validators.required,
          Validators.minLength(5),
          Validators.maxLength(15)
        ])
      ],
      cnicNumber: [
        null,
        Validators.compose([
          Validators.required,
          Validators.minLength(13),
          Validators.maxLength(13)
        ])
      ],
      cnicExpiryDate: [
        null,
        Validators.compose([Validators.required, CustomValidators.date])
      ],
      date: [
        null,
        Validators.compose([Validators.required, CustomValidators.date])
      ],
      maritalStatus: [
        null,
        Validators.compose([Validators.required])
      ],
      educationLevel: [
        null,
        Validators.compose([
          Validators.required,
        ])
      ],
      phone: [
        null,
        Validators.compose([Validators.required])
      ],
      gender: [null, Validators.required],
      disability: [null, Validators.required],
      address: ['', Validators.required],
      occupation: ['', Validators.required],
      motherMaidenName: ['', Validators.required],
    });

    this.filteredOptions = this.form.controls['maritalStatus'].valueChanges.pipe(
      startWith<string | MaritalStatus>(''),
      map(value => (typeof value === 'string' ? value : value.name)),
      map(name => (name ? this._filter(name) : this.options1.slice()))
    );
  };

  triggerResize() {
    // Wait for changes to be applied, then trigger textarea resize.
    this.ngZone.onStable
      .pipe(take(1))
      .subscribe(() => this.autosize.resizeToFitContent(true));
  };

  private _filter(name: string): MaritalStatus[] {
    const filterValue = name.toLowerCase();

    return this.options1.filter(
      option => option.name.toLowerCase().indexOf(filterValue) === 0
    );
  };

  displayFn(maritalStatus?: MaritalStatus): string | undefined {
    return maritalStatus ? maritalStatus.name : undefined;
  };

  onSubmitClientRegistrationForm() {
    console.log(this.form.value);
  };

}
