@Component({
  selector: 'app-additional-information',
  templateUrl: './additional-information.component.html',
  styleUrls: ['./additional-information.component.scss']
})
export class AdditionalInformationComponent implements OnInit, AfterViewInit {


  @ViewChild(MatTable, { static: true }) table: MatTable<any> = Object.create(null);
  searchText: any;
  displayedColumns: string[] = ['#', 'name', 'lastName', 'cnicNumber', 'dateOfBirth', 'occupation', 'maritalStatus', 'cnicExpiryDate', 'mobile', 'relationship', 'fatherSpzFirstName', 'fatherSpzLastName', 'address', 'action'];


  dataSource = new MatTableDataSource(additionalInformationDummyData);

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator = Object.create(null);


  constructor(public dialog: MatDialog, public datePipe: DatePipe) { }

  ngOnInit() {

  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  openDialog(action: string, obj: any) {
    // debugger;
    obj.action = action;

    const dialogRef = this.dialog.open(AdditionalInformationDialogContent, {
      data: obj
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result.event === 'Add') {
        this.addRowData(result.data);
      } else if (result.event === 'Update') {
        this.updateRowData(result.data);
      } else if (result.event === 'Delete') {
        this.deleteRowData(result.data);
      }
    });
  }

  addRowData(row_obj: AdditionalInformation) {
    this.dataSource.data.push({
      id: additionalInformationDummyData.length + 1,
      Name: row_obj.Name,
      LastName: row_obj.LastName,
      FatherSpzLastName: row_obj.FatherSpzLastName,
      FatherSpzFirstName: row_obj.FatherSpzFirstName,
      CnicNumber: row_obj.CnicNumber,
      MaritalStatus: row_obj.MaritalStatus,
      Gender: row_obj.Gender,
      Occupation: row_obj.Occupation,
      CnicExpiryDate: new Date(),
      DateOfBirth: new Date(),
      Relationship: row_obj.Relationship,
      Mobile: row_obj.Mobile,
      Address: row_obj.Address,
      imagePath: row_obj.imagePath
    });
    this.dialog.open(AddComponent);

    this.table.renderRows();

  }

  updateRowData(row_obj: AdditionalInformation) {
    this.dataSource.data = this.dataSource.data.filter((value) => {
      if (value.id === row_obj.id) {
        value.Name = row_obj.Name;
        value.LastName = row_obj.LastName;
        value.Relationship = row_obj.Relationship;
        value.MaritalStatus = row_obj.MaritalStatus;
        value.FatherSpzLastName = row_obj.FatherSpzLastName;
        value.FatherSpzFirstName = row_obj.FatherSpzFirstName;
        value.Mobile = row_obj.Mobile;
        value.Occupation = row_obj.Occupation;
        value.Gender = row_obj.Gender;
        value.CnicExpiryDate = row_obj.CnicExpiryDate;
        value.DateOfBirth = row_obj.DateOfBirth;
        value.Address = row_obj.Address;
        value.imagePath = row_obj.imagePath;
      }
      return true;
    });
  }

  deleteRowData(row_obj: AdditionalInformation) {
    this.dataSource.data = this.dataSource.data.filter((value) => {
      return value.id !== row_obj.id;
    });
  }






}

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'dialog-content',
  templateUrl: 'dialog-content.html',
})
// tslint:disable-next-line: component-class-suffix
export class AdditionalInformationDialogContent {
  action: string;
  local_data: any;
  selectedImage: any = '';
  joiningDate: any = ''

  constructor(public datePipe: DatePipe,
    public dialogRef: MatDialogRef<AdditionalInformationDialogContent>,
    // @Optional() is used to prevent error if no data is passed
    @Optional() @Inject(MAT_DIALOG_DATA) public data: AdditionalInformation) {

    // debugger;
    //  console.log(data);
    this.local_data = { ...data };
    this.action = this.local_data.action;

    if (this.local_data.CnicExpiryDate !== undefined) {
      this.joiningDate = this.datePipe.
        transform(new Date(this.local_data.CnicExpiryDate), 'yyyy-MM-dd');
      // this.local_data.CnicExpiryDate=this.local_data.CnicExpiryDate.t 
    }

    if (this.local_data.imagePath === undefined) {
      this.local_data.imagePath = 'assets/images/users/default.png'
    }
  }

  doAction() {
    this.dialogRef.close({ event: this.action, data: this.local_data });
  }

  closeDialog() {
    this.dialogRef.close({ event: 'Cancel' });
  }

  selectFile(event: any) {
    // debugger;
    if (!event.target.files[0] || event.target.files[0].length == 0) {
      //this.msg = 'You must select an image';
      return;
    }
    var mimeType = event.target.files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      //this.msg = "Only images are supported";
      return;
    }
    var reader = new FileReader();
    reader.readAsDataURL(event.target.files[0]);
    reader.onload = (_event) => {
      this.local_data.imagePath = reader.result;
    }
  }

}
